import React from 'react';

class Podcast extends React.Component{
    
    static async getInitialProps({query}){
        const podcastId = query.id;
        
        return{ podcastId }
    }
    
    render(){
        return(
            <div>
                <header>Podcasts</header>
                <h1>Hola desde el podcast { this.props.podcastId }</h1>


                <style jsx>{`
                header {
                color: #fff;
                background: #8756ca;
                padding: 15px;
                text-align: center;
                `}
                </style>
                <style jsx global>{`
                    body {
                    margin: 0;
                    font-family: system-ui;
                    background: white;
                    }
                `}</style>
            </div>
        )
    }
}

export default Podcast;